@charset "utf-8";

/* member_area 영역 시작 */
#member_area {
    width: 630px;
    margin-right: 30px;
    position: relative;
}
.location {
    position: absolute;
    top: 18px;
    right: 30px;
}
.location strong {
    color: #51a41e;
}
#member_area h2 {
    font-size: 1.5em;
    font-family: "NanumEX";
    color: #ff3c00;
    text-shadow: 1px 1px 0 rgba(0, 0, 0, 0.2);
    border-bottom: 2px solid #ff6738;
    padding-top: 15px;
    padding-bottom: 15px;
    margin-bottom: 20px;
}
.form_sign {
    text-align: right;
    margin: 30px 0 10px;
}
.require {
    display: inline-block;
    width: 10px;
    height: 12px;
    text-indent: 10px;
    white-space: nowrap;
    overflow: hidden;
}
.require::before {
    font-family: "webcafeIcon";
    content: "s";
    color: #f48002;
    position: relative;
    left: -10px;
}
#join_form {
    border-top: 2px solid #ccc;
}
#join_form p {
    border-bottom: 1px solid #ccc;
    overflow: hidden;
    position: relative;
}
#join_form label, .mailing em {
    float: left;
    width: 120px;
    padding: 13px 20px;
    margin-right: 10px;
    font-weight: bold;
    color: #666;
    background: #fffcef;
}
#join_form .mailing label {
    float: none;
    padding: 0;
    width: auto;
    background: none;
}
#join_form input {
    margin: 10px 0;
}
#join_form input:not([type="radio"]){
    border: 1px solid #aaa;
    height: 18px;
    padding-left: 5px;
}
#join_form input[type="radio"]{
    margin: 0;
}
a.check_id, a.find_zipcode {
    font-family: "NanumEX";
    font-size: 0.9em;
    background-color: #fe8389;
    color: #fff;
    padding: 3px 15px;
    margin-left: 5px;
    border-radius: 20px;
    text-shadow: 1px 1px 1px #ed676c, 1px -1px 1px #ed676c;
}
.find_zipcode {
    position: absolute;
    left: 230px;
    top: 12px;
}
input#address {
    display: block;
    margin: 0;
}
.label_address {
    height: 50px;
    line-height: 50px;
}
.mailing em {
    font-style: normal;
    height: 35px;
    line-height: 35px;
}
.mailing span {
    display: block;
    margin: 10px 0;
}
.btn_join_area {
    text-align: center;
    margin: 15px 0;
}
.btn_join_area button {
    font-family: "NanumEX";
    font-size: 1.2em;
    color: #fff;
    padding: 8px 20px;
    margin: 0 5px;
    border: 0;
    border-radius: 3px;
    letter-spacing: 1px;
}
.btn_ok::before {
    font-family: "webcafeIcon";
    content: "h";
    font-size: 2em;
    line-height: 0;
    position: relative;
    top: 7px;
    left: -5px;
}
.btn_ok {
    background-image: linear-gradient(to bottom, darkgray 0%, #c9c9c9 50%, black 100%);
}
.btn_cancel {
    background: linear-gradient(to bottom, rgba(145, 145, 145,0.8) 0%, rgb(160, 150, 155) 50%, rgb(197, 176, 176) 100%);
}
/* member_area 영역 종료 */

