@charset "utf-8";
/* useful_area 시작 */
#useful_area {
    width: 250px;
    margin-right: 30px;
    /*float: left;*/
}
/* 로그인 영역 시작*/
.login {
    background-image: radial-gradient(circle at top right, #ffc848, #ff8208);
    border-radius: 5px;
    padding: 10px;
    box-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
}
.login h2 {
    color: #feff9a;
    font-size: 1.2em;
    padding: 0 0 10px 10px;
}
.login label {
    display: inline-block;
    width: 48px;
}
.login input {
    width: 93px;
    height: 18px;
    padding: 1px 1px 1px 4px;
    border: 1px solid #ccc;
}
#user_id {
    margin-bottom: 6px;
}
.login form {
    position: relative;
    background-color: #fff;
    border-radius: 3px 3px 0 0;
    padding: 10px 10px 0 10px;
}
.login button {
    position: absolute;
    top: 14px;
    right: 5px;
    width: 50px;
    height: 50px;
    border: 1px solid #ff4e00;
    border-radius: 3px;
    background-image: linear-gradient(to bottom, #fe5f18 0%, #fa4306 100%);
    color: #fff;
    font-weight: bold;
    font-size: 0.9em;
}
.login fieldset {
    border-bottom: 1px solid #ccc;
    padding-bottom: 0 0 10px 0;
}
.member_link {
    background-color: #fff;
    border-radius: 0 0 3px 3px;
    padding: 0 10px;
    overflow: hidden;
}
.member_link a::before {
    font-family: "webcafeIcon";
    content: "g";
    color: #fa3d00;
    position: relative;
    top: 2px;
}
.join {
    float: left;
    padding: 10px 0;
}
.find {
    float: right;
    padding: 10px 0;
}
/* 로그인 영역 종료 */

/* 유효성 검사 영역 시작 */
.jumplink {
    margin-top: 20px;
}
.jumplink li {
    border: 1px solid #ccc;
    margin-bottom: 10px;
    border-radius: 20px;
    background-image: linear-gradient(to bottom, #fff 0%,#ebebeb 100%);
}
.jumplink .kakao a{
    display: block;
    height: 30px;
    color: #000;
    font-weight: bold;
    line-height: 30px;
    background: url('../public/images/kakaopage.png') no-repeat 20px 5px;
    background-size: 30px 20px;
    text-indent: 45px;
}
.jumplink .munpia a{
    display: block;
    height: 30px;
    color: #000;
    font-weight: bold;
    line-height: 30px;
    background: url('../public/images/munpia.jpg') no-repeat 20px 5px;
    background-size: 20px 20px;
    text-indent: 45px;
}
/* 유효성 검사 영역 종료 */

/* 웹 관련 용어 영역 시작 */
.term {
    margin-bottom: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
    padding: 15px 20px;
    background-image: linear-gradient(to bottom, #fff 0%, #f4f4f4 100%);
}
.term > h2 {
    font-size: 1.2em;
}
.term dl {
    margin-top: 15px;
    overflow: hidden;
}
.term dt {
    color: #296897;
    float: right;
    font-weight: bold;
    margin-bottom: 5px;
    width: 130px;
}
.term dd:nth-of-type(1) {
    float: left;
}
.term dd:nth-of-type(2) {
    float: right;
    width: 130px;
    line-height: 1.45;
    text-align: justify;
}
/* 웹 관련 용어 영역 종료 */

/* useful_area 종료 */

/* info_area 종료 */
#info_area {
    width: 390px;
    margin-right: 30px;
    /*float: left;*/
}

/* 검색 영역 시작*/
.search {
    border-radius: 15px 15px 0 0;
    background-image: linear-gradient(to bottom,#ddd 0%,#f8f8f8 50%, #fff 100%);
}
.search label {
    padding: 2px 0 4px 2px;
    font-weight: bold;
    color: #999;
}
.search label::before {
    font-family: "webcafeIcon";
    content: "z";
    font-size: 1.5em;
    color: #7d7d7d;
    font-weight: normal;
    margin-right: 5px;
    position: relative;
    top: 5px;
}
#keyword {
    width: 196px;
    height: 18px;
    border: 1px solid #ccc;
    border-radius: 3px;
    margin: 0 5px;
}
.search button {
    padding: 3px 10px;
    background: linear-gradient(to bottom, #9c9da1 0%, #666 30%, #4e4f56 100%);
    color: #fff;
    border: 1px solid #949494;
    border-radius: 3px;
    font-weight: bold;
}
/* 검색 영역 종료*/

/* 공지사항 및 자료실 영역 시작*/
.board {
    margin-top: 20px;
    height: 170px;
    position: relative;
}
.board section {
    position: relative;
}
.board section:target {
    z-index: 10;
}
.board h2 {
    float: left;
    font-size: 1em;
}
.board h2 a {
    display: inline-block;
    padding: 6px 15px;
    color: #7a7a7a;
    font-family: "NanumEX";
    border: 1px solid #c9c9c9;
    border-top-left-radius: 5px;
    border-top-right-radius: 5px;
    background-image: linear-gradient(to bottom, #fcfcfc 0%, #eaeaea 100%);
}
.board section:target h2 a {
    color: #ff4911;
    border: 1px solid #ff6738;
    border-bottom: 1px solid #fff;
    background: #fff;
    border-radius: 5px 5px 0 0;
}
.board::before {
    content: "";
    position: absolute;
    left: 0;
    top: 27px;
    width: 362px;
    height: 1px;
    background-image: linear-gradient(to right, #e3e3e3 0%, #fff 100%);
}
.board ul {
    position: absolute;
    top: 40px;
    left: 0;
    width: 100%;
    background-color: #fff;
}
.board li {
    margin-bottom: 10px;
}
.board ul li a {
    display: inline-block;
    width: 300px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}
.board ul a::before{
    font-family: "webcafeIcon";
    content: "r";
    color: #ccc;
    position: relative;
    top:2px
}
.board time {
    float: right;
    color: #7a7a7a;
}
.board .more {
    position: absolute;
    top: 0;
    right: 0;
    padding: 5px;
}
.more::before {
    font-family: "webcafeIcon";
    content: "p";
    color: #00b745;
    margin-right: 3px;
    position: relative;
    top: 2px;
}
/* 공지사항 및 자료실 영역 종료*/

/* 새소식 영역 종료*/
.news {
    margin-top: 20px;
    position: relative;
}
.news::before {
    content: "";
    position: absolute;
    left: 0;
    top: 27px;
    width: 362px;
    height: 1px;
    background-image: linear-gradient(to right, #e3e3e3 0%, #fff 100%);
}

.news > h2 {
    color: #ff5a00;
    font-size: 1.2em;
}
.news .more {
    position: absolute;
    top: 0;
    right: 0;
    padding: 8px;
}
.news article {
    margin: 25px 0 0 130px;
}
.news article h3 {
    margin-bottom: 10px;
    font-size: 1em;
}
.news article h3::before {
    font-family: "webcafeIcon";
    content: "r";
    color: #666;
    position: relative;
    top: 2px;
}
.news figure {
    position: absolute;
    top: 45px;
    left: 0;
    text-align: center;
}
.news figure img {
    box-shadow: 0 10px 15px #eee;
    margin-bottom: 15px;
}
.news time {
    font-size: 0.9em;
    margin-left: 12px;
}
.news p {
    margin-top: 10px;
    margin-left: 12px;
    text-align: justify;
    line-height: 1.8;
}
/* 새소식 영역 종료*/
/* info_area 종료 */

/* link_area  */
#link_area {
    width: 200px;
    margin-right: 30px;
    /*float: left;*/
}
.eventrel {
    border: 1px solid #949494;
    border-radius: 10px;
    padding: 10px;
    background-image: linear-gradient(to bottom, #fff 0%, #f4f4f4 100%);
    padding: 10px 13px;
}
/* 신규이벤트 영역 시작 */
.event {
    position: relative;
}
.event h2 {
    font-size: 1.2em;
    padding-bottom: 10px;
}
.event span {
    color: #ff3c00;
}
.event img {
    border: 1px solid #eee;
    box-shadow: 4px 4px 4px rgba(0, 0, 0, 0.2);
}
.event p {
    padding: 10px;
    line-height: 1.4;
}
.event em {
    font-style: normal;
    display: block;
}
.btn_event_area {
    position: absolute;
    top: 0;
    right: 0;
}
.btn_event_area a {
    display: inline-block;
    width: 20px;
    height: 20px;
    white-space: nowrap;
    overflow: hidden;
    text-indent: 20px;
    background-image: url('../public/images/back_forward.png');
    background-repeat: no-repeat;
}
.btn_event_prev {
    background-position: 0 0;
}
.btn_event_next {
    background-position: 100% 0;
}
/* 신규이벤트 영역 종료 */

/* 관련사이트 영역 시작 */
.related_link h2 {
    font-size: 1.2em;
    padding: 10px 0;
    border-top: 1px solid #ccc;
}
.related_link span {
    color: #ff3c00;
}
.related_link ul {
    height: 25px;
    overflow: hidden;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #fff;
    transition: height 0.2s;
}
.related_link ul:hover {
    height: 125px;
    padding: 10px 0;
}
.related_link li {
    height: 25px;
    line-height: 25px;
    text-indent: 1.5em;
}
.related_link a {
    display: block;
}
/* 관련사이트 영역 종료 */

/* 인기사이트 영역 시작 */
.hot_link {
    margin-top: 20px;
    position: relative;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-image: linear-gradient(to bottom, #fff 0%, #f4f4f4 100%);
    padding: 10px 12px 8px 10px;
}
.hot_link h2 {
    font-size: 1.2em;
}
.hot_link span {
    color: #ff3c00;
}
.hot_link .more {
    position: absolute;
    top: 8px;
    right: 6px;
    padding: 8px;
}
.hot_link ol {
    margin-top: 5px;
    padding: 0;
    overflow: hidden;
}
.hot_link li {
    position: relative;
    margin-top: 7px;
    counter-increment: number;
}
.hot_link li::before {
    content: counter(number, decimal);
    padding: 2px 5px;
    border-radius: 3px;
    background-color: #a1a1a1;
    margin-right: 5px;
    color: #fff;
    font-size: 0.75em;
}
.hot_link li:last-child {
    margin-bottom: 2px;
}
.hot_link em {
    float: right;
    width: 8px;
    height: 0;
    overflow: hidden;
    padding-top: 12px;
    background: url('../public/images/rank.png') no-repeat 0 0;
}
.hot_link .up {
    background-position: 0 0;
}
.hot_link .stop {
    background-position: 0 50%;
}
.hot_link .down {
    background-position: 0 100%;
}
/* 인기사이트 영역 종료 */

